<template lang="html">
  <section :class="$style.content">
    <div :class="$style.navTop">
      <span :class="$style.goback" @click="goback()"></span>
      <span :class="$style.textHeader">京东登录</span>
    </div>
  </section>
</template>

<script>
export default {
  methods: {
    goback () {
      window.history.go(-1)
    }
  }
}
</script>

<style lang="scss" module>
.content{
  background-color: #ffffff;
  position: fixed;
  top:0;
  left:0;
  right:0;
  z-index: 101;
  .navTop{
    position: relative;
    width:100%;
    height:88px;
    line-height: 88px;
    font-size: 34px;
    text-align: center;
    .goback{
      position: absolute;
      top:20px;
      left:40px;
      width: 48px;
      height: 48px;
      overflow: hidden;
      display: block;
      background-image: url("data:image/png;base64,");
      background-repeat: no-repeat;
      background-size: 100% auto;
    }
    .textHeader{

    }
  }
}
</style>



